.accessible {
    margin-bottom: $default-margin-small;
}

@include media-breakpoint-up(md) {

    .element .poi-head {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}

.acc-icon {
    position: relative;
    width: $acc-icon-size;
    padding: 0 0 $acc-icon-size 0!important;
    display: inline-block;
    color: $acc-icon-text-color;
    background-color: $acc-icon-bg-color;
    border-radius: $acc-icon-border-radius;
    margin-right: 10px;
    font-size: $h3-font-size;

    &:last-of-type {
        margin-right: 0;
    }

    .acc-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        display: flex;
    }

    .fa {
        font-weight: normal;
    }

    .fa-male {
        position: relative;
        margin: 0 1px 0 2px;
        font-weight: normal;

        &::after {
            display: block;
            content: "";
            border-right: 1px solid $acc-icon-text-color;
            position: absolute;
            height: 100%;
            bottom: 0;
            right: -2px;
        }
    }

    .fa-female {
        font-weight: normal;
        transform: scale(0.85, 1);
    }
}
